        </path>
      </svg>
    </div>
    <div class="app-container" v-show="loaded === 80">
      <div class="menu-container">
        <img class="left" src="../src/assets/logo.svg">
        <div class="mid"><span>{{timeMinSec[0]}}</span>:<span>{{timeMinSec[1]}}</span></div>
        <div class="right">
          <div class="buttons">
            <b-button type="is-primary" icon-right="undo-alt" v-on:click="reset" title="Reset"></b-button>
            <b-button type="is-info" icon-right="volume-up" title="Mute" v-show="!muted" v-on:click="mute" ></b-button>
            <b-button type="is-info" icon-right="volume-mute" title="Unmute" v-show="muted" v-on:click="unmute" ></b-button>
            <b-button type="is-warning" icon-right="info" title="Info" v-on:click="infoActive = true" ></b-button>
          </div>
        </div>
      </div>
      <div class="game-container">
        <div class="one" v-bind:style="computedStyle">
          <video muted loop autoplay v-show="state === 0" v-on:canplaythrough.self.once="loaded++">
            <source id="mp4" src="../src/assets/videos/menu.mp4" type="video/mp4">
            <p>Your user agent does not support the HTML5 Video element.</p>
          </video>
          <img v-for="n in 74" :key="n" :src="frames[n - 1]" v-show="state === 1 && (counter === n || (counter === 0 && n === 1))" v-on:load.self.once="loaded++">
          <video muted loop autoplay v-show="state === 2" v-on:canplaythrough.self.once="loaded++">
            <source id="mp4" src="../src/assets/videos/gameover-text.mp4" type="video/mp4">
            <p>Your user agent does not support the HTML5 Video element.</p>
          </video>
        </div>
        <div class="two"></div>
        <div v-bind:style="computedStyle">
          <div id="konvacanvas" class="stack" v-bind:style="computedStyle"></div>
          <div class="stack startmenu" v-bind:style="computedStyle" v-show="state === 0">
            <div class="center">
              <div class="startmenu-text">Click on Kaa's head to resist him.</div>
              <div class="startmenu-button center"><b-button type="is-primary" v-on:click="start">Start</b-button></div>
            </div>
          </div>
          <div class="stack rings" v-bind:style="opacity" v-show="state === 1 || state === 2">
            <video muted loop autoplay v-on:canplaythrough.self.once="loaded++">
              <source id="mp4" src="../src/assets/videos/rings.mp4" type="video/mp4">
              <p>Your user agent does not support the HTML5 Video element.</p>
            </video>
          </div>
