              <li>'The Jungle Book' belongs to Disney</li>
              <li>'Kaa ASMR' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UCdFPB-7oRVyT74kBaDwid5Q" target="_blank">m5a5e5</a></li>
              <li>'Hypnotic Rings' by <a class="has-text-weight-bold" href="https://www.youtube.com/channel/UC8xGQOErVZSlM3PRHjoMe0A" target="_blank">K VFX</a></li>
            </ul>
          </div>
        </section>
        <footer class="modal-card-foot"></footer>
      </div>
    </b-modal>
  </div>
</template>

<script>
const Konva = require('konva')

export default {
  name: 'App',
  data: function () {
    return {
      bodyWidth: 0,
      bodyHeight: 0,
      konvaCache: {},
      state: 0,
      counter: 0,
      time: 0,
      intervall: 1000,
      frames: [],
      sounds: {},
      loaded: 0,
      timeouts: {},
      muted: false,
      infoActive: false
    }
  },
  computed: {
    timeMinSec: function () {
      let minutes = Math.floor(this.time / 60)
      let seconds = this.time - minutes * 60
      if (minutes < 10) minutes = `0${minutes}`
      if (seconds < 10) seconds = `0${seconds}`
      return [minutes, seconds]
    },
    dimensions: function () {
      const width = Math.floor(((this.bodyWidth - 35) / 2))
      const height = Math.floor(1072 * (width / 1872))
      return [width, height]
    },
    computedStyle: function () {
      return {
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    },
    opacity: function () {
      let opacity = 0
      if (this.state === 2) {
        opacity = 1
      } else {
        if (this.counter > 8) opacity = (this.counter - 9) * 1 / 65
      }
      return {
        opacity,
        width: `${this.dimensions[0]}px`,
        height: `${this.dimensions[1]}px`
      }
    }
  },
  watch: {
    dimensions: function () {
      this.konvaCache.stage.size({
        width: this.dimensions[0],
        height: this.dimensions[1]
      })
      this.redrawKaa()
    }
  },
  methods: {
    updateBodyDimensions: function () {
      this.bodyWidth = document.body.offsetWidth
      this.bodyHeight = document.body.bodyHeight
      this.konvaCache.scale = 1
      if (this.bodyWidth < 1700) this.konvaCache.scale = Math.max(this.bodyWidth / 1700, 0.5)
    },
    initKonva: function () {
      const stage = new Konva.Stage({
        container: 'konvacanvas',
        width: this.dimensions[0],
        height: this.dimensions[1]
      })
      const layer = new Konva.Layer()
      stage.add(layer)

      const imageObj = new Image()
      imageObj.src = require('../src/assets/kaa-small-1.png')
      const kaa = new Konva.Image({
        image: imageObj,
        width: 80,
        height: 65
      })
      kaa.on('mousedown', (event) => {
        event.target.destroy()
        layer.batchDraw()
        this.counter--
      })
      kaa.on('touchstart', (event) => {
        event.target.destroy()
        layer.batchDraw()
        this.counter--
      })
      this.konvaCache.kaa = kaa
      this.konvaCache.stage = stage
      this.konvaCache.layer = layer
    },
    initFrames: function () {
      for (let i = 1; i < 75; i++) {
        this.frames.push(require(`../src/assets/frames/frame-${i}.jpg`))
      }
    },
    initSounds: function () {
      this.sounds.gulp = new Audio(require('../src/assets/sounds/gulp.mp3'))
      this.sounds.gulp.addEventListener('canplaythrough', () => this.loaded++, { once: true })
      this.sounds.ping = new Audio(require('../src/assets/sounds/ping.mp3'))
      this.sounds.ping.addEventListener('canplaythrough', () => this.loaded++, { once: true })
      this.sounds.asmr = new Audio(require('../src/assets/sounds/asmr.mp3'))
      this.sounds.asmr.addEventListener('canplaythrough', () => this.loaded++, { once: true })
      this.sounds.asmr.volume = 0.5
    },
    addKaa: function () {
      const clone = this.konvaCache.kaa.clone({
        scale: {
          x: this.konvaCache.scale,
          y: this.konvaCache.scale
        },
        x: Math.random() * (this.konvaCache.stage.width() - 80) * this.konvaCache.scale,
        y: Math.random() * (this.konvaCache.stage.height() - 65) * this.konvaCache.scale
      })
      this.konvaCache.layer.add(clone)
      this.konvaCache.layer.batchDraw()
    },
    redrawKaa: function () {
      const kaas = this.konvaCache.layer.getChildren()
      kaas.each((kaa) => {
        kaa.scaleX(this.konvaCache.scale)
        kaa.scaleY(this.konvaCache.scale)
        kaa.x(Math.random() * (this.konvaCache.stage.width() - 80) * this.konvaCache.scale)
        kaa.y(Math.random() * (this.konvaCache.stage.height() - 65) * this.konvaCache.scale)
      })
      this.konvaCache.layer.batchDraw()
    },
    running: function () {
      if (this.state !== 1) return
      if (this.time > 0 && this.time % 5 === 0) {
        this.intervall = 1000 - Math.floor(300 * Math.log(this.time / 5 + 1))
      }
      this.timeouts.running = setTimeout(() => {
        if (this.state !== 1) return
        this.addKaa()
        this.counter++
        if (this.counter < 75) {
          if (!this.muted && this.counter === 57) this.sounds.gulp.play()
          this.running()
        } else {
          if (!this.muted) this.sounds.ping.play()
          this.state = 2
        }
      }, this.intervall)
    },
    timer: function () {
      this.timeouts.timer = setTimeout(() => {
        if (this.state !== 1) return
        if (this.counter < 75) {
          this.timer()
          this.time++
        }
      }, 1000)
    },
    start: function () {
      this.state = 1
      this.running()
      this.timer()
      setTimeout(() => {
        this.sounds.asmr.currentTime = 0
        this.sounds.asmr.play()
      }, 2000)
    },
    stop: function () {
      if (this.timeouts.running) {
        clearTimeout(this.timeouts.running)
        this.timeouts.running = null
      }
      if (this.timeouts.timer) {
        clearTimeout(this.timeouts.timer)
        this.timeouts.timer = null
      }
      this.sounds.asmr.pause()
      this.sounds.asmr.currentTime = 0
    },
    reset: function () {
      this.stop()
      this.konvaCache.layer.destroyChildren()
      this.konvaCache.layer.batchDraw()
      this.counter = 0
      this.time = 0
      this.intervall = 1000
      this.state = 0
    },
    mute: function () {
      this.sounds.gulpvolume = 0
      this.sounds.ping.volume = 0
      this.sounds.asmr.volume = 0
      this.muted = true
    },
    unmute: function () {
      this.sounds.gulpvolume = 1
      this.sounds.ping.volume = 1
      this.sounds.asmr.volume = 0.5
      this.muted = false
    }
  },
  mounted: function () {
    this.updateBodyDimensions()
    this.initKonva()
    this.initSounds()
    this.initFrames()
    window.addEventListener('resize', this.updateBodyDimensions)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.updateBodyDimensions)
  }
}
</script>

